/* ввод координат */

.inputCoord {
  display: block;
  width: 100%;
  border: none;
  background-color: #aaaaaa;
}

.inputCoord:focus {
  outline: none;
  border-bottom: 1px solid white;
}

.newCoord {
  display: block;
  width: 100%;
  padding: 0;
  border: none;
  background-color: #999;
}

.newCoord:focus {
  outline: none;
  border-bottom: 1px solid white;
}

