.ol-overlay-container {
  will-change: left, right, top, bottom;
}

.ol-unsupported {
  display: none;
}
.ol-viewport,
.ol-unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.ol-selectable {
  -webkit-touch-callout: default;
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}
.ol-grabbing {
  cursor: -webkit-grabbing;
  cursor: -moz-grabbing;
  cursor: grabbing;
}
.ol-grab {
  cursor: move;
  cursor: -webkit-grab;
  cursor: -moz-grab;
  cursor: grab;
}

.ol-overviewmap {
  position: absolute;
  bottom: 2em;
  left: 2em;
}

.ol-zoom {
  position: absolute;
  bottom: 1.5em;
  right: 0.5em;
}

.ol-zoom-in {
  padding: 0;
  display: flex;
  align-items: center;
}

.ol-zoom-out {
  padding: 0;
}

.ol-rotate {
  display: none;
}

@media print {
  .ol-control {
    display: none;
  }
}

.ol-control button {
  display: block;
  margin: 2px;
  padding: 0;
  color: white;
  font-size: 1.3em;
  text-decoration: none;
  text-align: center;
  height: 24px;
  width: 24px;
  background-color: #303030;
  border: none;
  border-radius: 2px;
}

.ol-control button:hover {
  background-color: #2ea297;
}

.ol-control button:hover {
  text-decoration: none;
  opacity: 1;
  outline: none;
}

.ol-control button:focus {
  outline: none;
}

